import React, { Component } from 'react';

import FadeIn from '../transitions/fade-in';
import CharacterBox from './characterBox';
import ScoreDisplay from './scoredisplay';
import HighScore from './scoredisplay';

const shuffleArray = arr => (
    arr
      .map(a => [Math.random(), a])
      .sort((a, b) => a[0] - b[0])
      .map(a => a[1]) 
);

const initialChars = [
    {
        name: 'Camo Guitar',
        img: 'img/images/camoGuitar.png',
        clicked: false
    },
    {
        name: 'Destroyer',
        img: 'img/images/destroyer.png',
        clicked: false
    },
    {
        name: 'Funky Guitar',
        img: 'img/images/funkyGuitar.png',
        clicked: false
    },
    {
        name: 'Gibson',
        img: 'img/images/gibson.png',
        clicked: false
    
    }, 
    {  
        name: 'Headless Guitar',
        img: 'img/images/headless.png',
        clicked: false
    },
    {
        name: 'Fender Strat',
        img: 'img/images/strat.png',
        clicked: false
    },
    {
        name: 'Tobaco Acoustic',
        img: 'img/images/tobacoAcoustic.png',
        clicked: false
    },
    {
        name: 'Washburn Guitar',
        img: 'img/images/washburn.png',
        clicked: false
    }
]

export default class Board extends Component {

    constructor(props){
        super(props);

        this.state = {
            user: {
                score: 0 
            },
            characters: shuffleArray( initialChars )
        };
    }

    onCharacterClick = ( index ) =>{
        if( !this.state.characters[index].clicked ){
            this.setState({
                characters: shuffleArray( this.state.characters.map( (character, current) =>  {
                    return ( current === index ) ? { ...character, clicked:true } : character
                })),
                user: {
                    ...this.state.user,
                    score: this.state.user.score + 1
                }
            });
            //and shuffle
        } else {
            this.setState({
                characters: shuffleArray(this.state.characters.map( character => { return { ...character, clicked : false } })),
                user: {
                    ...this.state.user,
                    score: 0
                }
            });
            //and shuffle
        }
        
    }

    render(){
        return (
            <div className="Board">
                <FadeIn 
                    in={true}
                    duration={450}
                    length={'30px'}
                    direction={'bottom'}
                    delay={'1s'}>
                    <p>"Get your Jam on"<br/>Test your memory with this Guitar Stage Clicky Game! Simply click on a Guitar Model to recieve a point. But beware, clicking the same guitar twice resets the game. Rock on! Enjoy :)</p>
                </FadeIn>
                <FadeIn 
                    in={true}
                    duration={500}
                    direction={'bottom'}
                    delay={'1.5s'}>
                    <ScoreDisplay
                        score={this.state.user.score} />
                </FadeIn>
                <CharacterBox 
                    characters={this.state.characters} 
                    onCharacterClick={this.onCharacterClick} />
            </div>
        )
    }

}


